import React from 'react'
import Link from 'gatsby-link'
import './Card.css'

const Card = props => (

  <div className="CardContainer">
    <div className="Card">
        {/* <img src={props.image} />
        <h4>{props.category}</h4>
        <h3>{props.title}</h3>
        <p>{props.text}</p> */}



   <div className="clash-card">
      <div className="clash-card__image clash-card__image--archer">
        <img src={props.image} alt="archer" />
      </div>
      <div className="clash-card__level clash-card__level--archer">{props.category}</div>
      <div className="clash-card__unit-name">{props.title}</div>
      <div className="clash-card__unit-description">{props.text}</div>
    </div>




    </div>
  </div>

)

export default Card



