import React from 'react'
import Link from 'gatsby-link'
import face from '../images/face2.png'
import arrow2 from '../images/arrow2.png'
import mockup from '../images/mockup.png'
import ebay from '../images/ebay.png'
import daily from '../images/daily.png'
import ina from '../images/ina.png'
import tedx from '../images/tedx.png'


import Card from '../components/card'

const IndexPage = () => (
  <div>

    <div className="Main"> 

      <div className="MainGroupOne" data-bg-text="V">
        <div className="ContentOne">
          <div className="OneText">
            <h2>Hi, I'm <span className="underline victoria"><Link to="/about/">Victoria Ye</Link>
              <div className="yours messages">
                <div className="message last">
                  Who?
                </div>
              </div></span>
            </h2>
            <p>I'm so glad you were able to find your way here. Thanks for stopping by!</p>
          </div>
          <img src={face}></img>
        </div>
      </div>

      <div className="MainGroupTwo">
        <div className="ContentTwo">
          <h2>This is filler text because I have no idea how to brand myself as a designer!</h2>
        </div>
      </div>

      <div className="MainGroupThree">
        <div className="ContentThree">
          <h1><span className="overline">Latest work</span></h1>
          <p>Most recently, I was an intern at eBay (an amazing experience that I highly recommend).</p>
          <img src={arrow2} className="arrowtwo"></img>
            <Card image={ebay} category="Internship" title="Design at eBay" text="During the summer of 2018, I was a product design intern at eBay under the Buyer Experience team."/>
        </div>
      </div>

      <div className="MainGroupFour">
        <div className="ContentFour">
          <h1><span className="overline">All work</span></h1>
          <div className="CardGroup">
            <Link to="/coursera/"><Card image={mockup} category="UX Design" title="Coursera Redesign" text="Exploring ways to make Coursera's platform more conducive to social learning."/></Link>
            <Card image={ina} category="UX Research/Design" title="INA: Conversational User Interface Design" text="A design for a voice-based CUI to support and facilitate healthy eating habits."/>
            <Card image={daily} category="UI Design" title="Daily UI Challenge" text="Testing new interactions and learning new tools through daily UI prompts."/>
            <Card image={tedx} category="Identity/Print" title="TEDxCMU Design" text="Creating and maintaining a strong visual identity for the organization and annual conference."/>
          </div>
        </div>
      </div>


    </div>

    <div className="background" id="background1"></div>
    <div className="background" id="background2"></div>
    <div className="background" id="background3"></div>

  </div>
)

export default IndexPage



